$greyDark: #040404;
$greyLight: #131313;
$greyLighter: #282828;
$greyText: #b3b3b3;
$greyNav: #090909;
$greenMain: #1db954;

* {
  box-sizing: border-box;
}

a{
  color: inherit;
  text-decoration: none;
}
.App {
  height: calc(100vh -  90px);
  background: $greyLight;
  display: flex;

}

.subText {
  font-size: 0.9rem;
  color: $greyText;
}

.uppercase {
  text-transform: uppercase;
}
.smallText {
  font-size: 0.75rem;
  letter-spacing: 0.8px;
  &.bold{
  font-weight: 600;
  }
}

.navBar {
  width: 260px;
  height: 100%;
  background: $greyDark;
  display: flex;
  flex-direction: column;
  .logo {
    padding: 1rem 0.8rem;
    svg {
      g {
        fill: white;
      }
    }
  }
  ul {
    margin: 0;
    padding: 10px;
    a {
      color: inherit;
      text-decoration: none;
      &:visited {
        color: inherit;
        text-decoration: none;
      }
    }
    li {
      color: $greyText;
      list-style-type: none;
      border-radius: 5px;
      padding: 0.5rem 0.8rem;
      cursor: pointer;
      transition: all 0.3s ease-in-out;
      font-size: 0.9rem;
      display: flex;
      align-items: center;
      svg {
        fill: currentColor;
        margin-right: 1rem;
      }
      &.active {
        background: $greyLighter;
        color: white;
        svg {
          fill: white;
        }
      }
      &:hover {
        color: white;
      }
    }
  }
  .cookies {
    font-size: 0.6rem;
    color: $greyText;
    text-align: center;
    margin-top: auto;
    padding: 1rem;
    span {
      display: block;
    }
  }
}

.outerWrap {
  background: grey;
  height: 100vh;
  min-height: 100%;
}

.musicControls {
  background: $greyLighter;
  text-align: center;
  height: 90px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  z-index: 2;
  position: relative;
  border-top: 1px solid $greyDark;
  .play {
    height: 40px;
    width: 40px;
    margin-left: 1em;
    margin-right: 1em;
  }
}

.upperNav {
  background: $greyNav;
  height: 56px;
  width: 100%;
  display: flex;
  align-items: center;
  color: white;
  padding: 0.5rem 2rem;
  position: fixed;
  top: 0;
  z-index: 2;
}

.main {
  width: 100%;
  position: relative;
  overflow-y: scroll;
  padding-top: 3.4rem;
  .mainContent {
    color: white;
    padding: 0.7rem 2rem;
    h1,h2 {
      font-size: 1.8rem;
    }
  }
}

.cardsWrap{
  padding-bottom: 1.8rem;
  h2 {
    margin-bottom: 0;
  }
  .cardsWrapInner {
    display: grid;
    grid-gap: 16px;
    grid-template-columns: repeat(auto-fill, minmax(164px, 1fr));
  }
  .subText {
    margin-top: 0.5rem;
  }
  .card{
    background: $greyLighter;
    border-radius: 10px;
    overflow: hidden;
    padding: 1.2rem 1.2rem 1rem;
    position: relative;
    &:hover {
      .playIcon{
        opacity: 1;
      }
    }
    .cardImage {
      height: 150px;
      box-shadow: 0 10px 30px 0 rgba(0, 0, 0, 0.3), 0 1px 2px 0 rgba(0,0,0,0.2);
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    .cardContent {
      padding: 0.4rem 0;
      h3 {
        font-weight: 600;
        font-size: 0.8rem;
        letter-spacing: 0.6px;
      }
      span {
        font-size: 0.8rem;
        color: $greyText;
      }
    }
  }
}

.playIcon {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  overflow: hidden;
  display: flex;
  background: $greenMain;
  margin-left: auto;
  opacity: 0;
  position: absolute;
  right: 1.2rem;
  bottom: 1.2rem;
  svg {
    margin:auto;
  }
}

.playlistPage {
  display: flex;
  flex-direction: column;
  background: rgb(214,214,214);
  .mainInner {
    background-image: -webkit-gradient(
      linear, left top, left bottom,
      from (rgba(0, 0, 0, 0.5)),
      color-stop(40%, #121212)
    );
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), #121212 40%);
    width: 100%;
  }
  .playlistPageInfo{
    // width: 300px;
    display: flex;
    text-align: center;
    padding: 1.5rem;
    .playlistPageImage{
      width: 250px;
      height: 250px;
      img {
        height: 100%;
        width: 100%;
        object-fit: cover;
        -webkit-box-shadow: 0 4px 60px rgba(0,0,0,0.5);
        box-shadow: 0 4px 60px rgba(0,0,0,0.5);
      }
    }
  }
  .playlistPageSongs {
    height: 100%;
    .playlistButtons {
      display: flex;
      align-items: center;
    .playIcon{
      @extend .playIcon;
      opacity: 1;
      margin-left: 0;
      position: relative;
      height: 56px;
      width: 56px;
      right: 0;
      bottom: 0;
      svg {
        height: 26px;
        width: 26px;
      }
    }
    .icons {
      .icon {
        margin-left: 1.5rem;
      }
      .iconsHeart {
        width: 36px;
        height: 36px;
        svg {
          fill: white;
        }
      }
    }
  }
  }
  .playlistPageContent{
    text-align: left;
    padding: 1.5rem 2rem;

    h1 {
      font-size: 7rem;
      margin: 0;
      line-height: 1;
      word-spacing: -15px;
    }
    .tagline {
      font-size: 1rem;
      opacity: 0.7;
      margin-bottom: 0.5rem;
    }
    .playlistPageDesc {
      display: flex;
      align-items: center;
      p {
        margin: 0;
      }
      span {
        opacity: 0.7;
        position: relative;
        margin-left: 10px;
        &:before{
          content: '';
          width: 8px;
          height: 8px;
          background: inherit;
          overflow: hidden;
          border-radius: 50%;
          position: absolute;
          // left:
        }
      }
    }
  }
}

ul.songList {
  padding: 0;
  li {
    list-style: none;
    width: 100%;
    padding: 0.5rem;
    display: flex;
    align-items: center;
    transition: background 0.2s ease-in-out;
    .songDetails{
      h3 {
        margin: 0;
        font-weight: 300;
        font-size: 1rem;
      }
      span {
        color: rgba(255,255,255,0.7);
        font-size: 0.9rem;
      }
    }
    .songTime {
      margin-left: auto;
    }
    .songIcon {
      padding-right: 0.5rem;
      .playI {
        display: none;
      }
      svg {
        fill: rgba(255, 255, 255, 0.7);
        width: 15px;
        height: 15px;
      }
    }
    &:hover {
      background: rgba(255,255,255,0.1);
      .songIcon {
        .playI{
          display: block;
        }
        .noteI {
          display: none;
        }
      }
    }
  }
}